	module.exports = {"pathContext":{"docHtml":"<p><a name=\"1.0.1\"></a></p>\n<h2><a href=\"https://github.com/blesh/symbol-observable/compare/1.0.0...v1.0.1\">1.0.1</a> (2016-06-15)</h2>\n<h3>Bug Fixes</h3>\n<ul>\n<li><strong>bundlers:</strong> fix issue that caused some bundlers not to be able to locate <code>/lib</code> (#19) (<a href=\"https://github.com/blesh/symbol-observable/commit/dd8fdfe\">dd8fdfe</a>), closes [(#19](<a href=\"https://github.com/(/issues/19\">https://github.com/(/issues/19</a>) <a href=\"https://github.com/blesh/symbol-observable/issues/17\">#17</a></li>\n</ul>\n<p><a name=\"1.0.0\"></a></p>\n<h1><a href=\"https://github.com/blesh/symbol-observable/compare/0.2.4...v1.0.0\">1.0.0</a> (2016-06-13)</h1>\n<h3>Bug Fixes</h3>\n<ul>\n<li><strong>index.js:</strong> use typeof to check for global or window definitions (#8) (<a href=\"https://github.com/blesh/symbol-observable/commit/5f4c2c6\">5f4c2c6</a>)</li>\n<li><strong>types:</strong> use default syntax for typedef (<a href=\"https://github.com/blesh/symbol-observable/commit/240e3a6\">240e3a6</a>)</li>\n<li><strong>TypeScript:</strong> exported ponyfill now works with TypeScript (<a href=\"https://github.com/blesh/symbol-observable/commit/c0b894e\">c0b894e</a>)</li>\n</ul>\n<h3>Features</h3>\n<ul>\n<li><strong>es2015:</strong> add es2015 implementation to support rollup (#10) (<a href=\"https://github.com/blesh/symbol-observable/commit/7a41de9\">7a41de9</a>)</li>\n</ul>\n<h3>BREAKING CHANGES</h3>\n<ul>\n<li>TypeScript: CJS users will now have to <code>require('symbol-observable').default</code> rather than just <code>require('symbol-observable')</code> this was done to better support ES6 module bundlers.</li>\n</ul>\n<p><a name=\"0.2.4\"></a></p>\n<h2><a href=\"https://github.com/blesh/symbol-observable/compare/0.2.2...v0.2.4\">0.2.4</a> (2016-04-25)</h2>\n<h3>Bug Fixes</h3>\n<ul>\n<li><strong>IE8 support:</strong> Ensure ES3 support so IE8 is happy (<a href=\"https://github.com/blesh/symbol-observable/commit/9aaa7c3\">9aaa7c3</a>)</li>\n<li><strong>Symbol.observable:</strong> should NOT equal <code>Symbol.for('observable')</code>. (<a href=\"https://github.com/blesh/symbol-observable/commit/3b0fdee\">3b0fdee</a>), closes <a href=\"https://github.com/blesh/symbol-observable/issues/7\">#7</a></li>\n</ul>\n<p><a name=\"0.2.3\"></a></p>\n<h2><a href=\"https://github.com/blesh/symbol-observable/compare/0.2.3...v0.2.3\">0.2.3</a> (2016-04-24)</h2>\n<h3>Bug Fixes</h3>\n<ul>\n<li><strong>IE8/ECMAScript 3</strong>: Make sure legacy browsers don't choke on a property named <code>for</code>. (<a href=\"https://github.com/blesh/symbol-observable/9aaa7c\">9aaa7c</a>)</li>\n</ul>\n<p><a name=\"0.2.2\"></a></p>\n<h2><a href=\"https://github.com/sindresorhus/symbol-observable/compare/0.2.1...v0.2.2\">0.2.2</a> (2016-04-19)</h2>\n<h3>Features</h3>\n<ul>\n<li><strong>TypeScript:</strong> add TypeScript typings file (<a href=\"https://github.com/sindresorhus/symbol-observable/commit/befd7a\">befd7a</a>)</li>\n</ul>\n<p><a name=\"0.2.1\"></a></p>\n<h2><a href=\"https://github.com/sindresorhus/symbol-observable/compare/0.2.0...v0.2.1\">0.2.1</a> (2016-04-19)</h2>\n<h3>Bug Fixes</h3>\n<ul>\n<li><strong>publish:</strong> publish all required files (<a href=\"https://github.com/sindresorhus/symbol-observable/commit/5f26c3a\">5f26c3a</a>)</li>\n</ul>\n<p><a name=\"0.2.0\"></a></p>\n<h1><a href=\"https://github.com/sindresorhus/symbol-observable/compare/v0.1.0...v0.2.0\">0.2.0</a> (2016-04-19)</h1>\n<h3>Bug Fixes</h3>\n<ul>\n<li><strong>Symbol.observable:</strong> ensure Symbol.for('observable') matches Symbol.observable (<a href=\"https://github.com/sindresorhus/symbol-observable/commit/ada343f\">ada343f</a>), closes <a href=\"https://github.com/sindresorhus/symbol-observable/issues/1\">#1</a> <a href=\"https://github.com/sindresorhus/symbol-observable/issues/2\">#2</a></li>\n</ul>","docPath":"argo-ci/node_modules/symbol-observable/changelog","proj":"argo-ci"}}

